	import BScroll from 'better-scroll'
	export default {
		props: { /** * 1 scroll * 2 scroll * 3 scrollswipescroll */
			probeType: {
				type: Number,
				default: 1
			},
			/** * click */
			click: {
				type: Boolean,
				default: true
			},
			/** *  */
			scrollX: {
				type: Boolean,
				default: false
			},
			/** *  */
			listenScroll: {
				type: Boolean,
				default: false
			},
			/** *  */
			data: {
				type: Array,
				default: null
			},
			/** *  */
			pullup: {
				type: Boolean,
				default: false
			},
			/** *  */
			pulldown: {
				type: Boolean,
				default: false
			},
			/** *  */
			beforeScroll: {
				type: Boolean,
				default: false
			},
			/** * scroll */
			refreshDelay: {
				type: Number,
				default: 20
			}
		},
		mounted() { // DOMbetter-scroll 
			setTimeout(() => {
				this._initScroll()
			}, 20)
//			/console.log(this.$refs.wrapper.querySelectorAll('.cRightList'))
		},
		methods: {
			_initScroll() {
				if(!this.$refs.wrapper) {
					return
				}
				// better-scroll
				this.scroll = new BScroll(this.$refs.wrapper, {
					probeType: this.probeType,
					click: this.click,
					scrollX: this.scrollX
				})
				//  
				if(this.listenScroll) {
					let me = this;
					this.scroll.on('scroll', (pos) => {
						this.$store.commit('SCROLL_Y',{scrollY:pos.y})
						me.$emit('scroll', pos)
					})
				}
				// 
				if(this.pullup) {
					this.scroll.on('scrollEnd', () => {
						// 
						if(this.scroll.y <= (this.scroll.maxScrollY + 50)) {
							this.$emit('scrollToEnd')
						}
					})
				}
				//  
				if(this.pulldown) {
					this.scroll.on('touchend', (pos) => {
						//  
						if(pos.y > 75) {
							this.$emit('pulldown')
						}
					})
				}
				//  
				if(this.beforeScroll) {
					this.scroll.on('beforeScrollStart', () => {
						this.$emit('beforeScroll')
					})
				}
			},
			disable() {
				// better-scrolldisable 
				this.scroll && this.scroll.disable()
			},
			enable() {
				// better-scrollenable
				this.scroll && this.scroll.enable()
			},
			refresh() {
				// better-scrollrefresh 
				this.scroll && this.scroll.refresh()
			},
			scrollTo() {
				// better-scrollscrollTo 
				this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
			},
			scrollBy(){
				// better-scrollscrollBy 
				this.scroll && this.scroll.scrollBy.apply(this.scroll, arguments)
			},
			scrollToElement() {
				// better-scrollscrollToElement 
				this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
			}
		},
		
		watch: {
			// refreshDelayrefresh
			data() {
				setTimeout(() => {
					this.refresh()
				}, this.refreshDelay)
			}
		}
	}



