
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import scroll from '../components/scroll'

export default {
directives: {
TransferDom
},
data() {
return {
insId: this.$route.params.insId, //id
insInfoList: [], //
qualityList: [], //
insInfoId: 0, //id
carInfo: [], //
tabIndex: 0, //tab
cLeftIndex: 0, //menu
imgPriviewList: [], //
carInfoTitle: '', 			insImgList: [],//
leftPartEachHeight: [] //
};
},
created() {
this._initData();
},
mounted() {

},
computed: {},
methods: {

async _initData() {
this.$vux.loading.show({
text: ''
});
await this.$http.get('/quote/info/' + this.insId).then((response) => {
let result = response.data;
if(result.code === 200) {
let ins = result.data.ins;
let insInfoList = result.data.insInfoList;
for(let i = 0; i < insInfoList.length; i++) {
let listRPI = [{
canShipDateBs: null,
reportPrice: null,
qualityRequirement: null
}];
insInfoList[i].listRPI = listRPI;
}
ins.rearImg&&this.insImgList.push({
img:ins.rearImg,
title:''
})
ins.fontImg&&this.insImgList.push({
img:ins.fontImg,
title:''
})
ins.driveLicense&&this.insImgList.push({
img:ins.driveLicense,
title:''
})
this.carInfoTitle = ins.carBrandName;
this.insInfoList = insInfoList;
this.insInfoId = this.insInfoList[0].id;
let qualityList = result.data.qualityList;
for(let i = 0; i < qualityList.length; i++) {
this.qualityList.push({
key: qualityList[i].id,
value: qualityList[i].propertyName
})
}
this.carInfo.splice(0, 0, {
label: "",
value: ins.carMark
}, {
label: "",
value: ins.carNo ? ins.carNo : ''
}, {
label: "vin",
value: ins.vin === '00000000000000000' ? '' : ins.vin
}, {
label: "",
value: ins.arriveTime
}, {
label: "",
value: ins.entMemberName
}, {
label: "",
value: ins.address
}, {
label: "",
value: ins.invoice == 1 ? '' : ''
})
} else if(result.code === 1) {
let vue = this
this.$vux.alert.show({
title: '',
content: result.msg,
onHide() {
vue.$router.push({
name: 'login'
})
}
})
} else {
this.$vux.alert.show({
title: '',
content: result.msg
})
}
}).catch((error) => {
console.log(error)
this.$vux.alert.show({
title: '',
content: ','
})
})
this.$vux.loading.hide();
},

showPartImg(index) {
if(this.insInfoList[index].groupSrc.length > 0) {
this.imgPriviewList = this.insInfoList[index].groupSrc;
this.$nextTick(() => {
this.$refs.previewer.show(0)
})
} else {
this.$vux.toast.text('', 'bottom');
}

},

choosePart(insInfoId, index) {
this.cLeftIndex = index;
},
addMoreQuality() {
this.insInfoList[this.cLeftIndex].listRPI.push({
canShipDateBs: null,
reportPrice: null,
qualityRequirement: null
});
},
delMoreQuality(index) {
this.insInfoList[this.cLeftIndex].listRPI.splice(index, 1);
}
},
components: {
Cell,
Group,
Tab,
TabItem,
PopupPicker,
Selector,
Datetime,
XInput,
XTextarea,
Flexbox,
FlexboxItem,
CellFormPreview,
scroll,
Previewer,
XButton,
Checker,
CheckerItem,
Swiper
}
}



