
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

import scroll from '../components/scroll'
export default {
components: {
XHeader,
Cell,
Group,
Tabbar,
TabbarItem,
Tab,
TabItem,
scroll,
Divider,
LoadMore,
Badge
},
data() {
return {
quoteList: [],
pageNum: 1,
refreshing: false,
tabIndex: 0,
loading: false
};
},
created() {
this._initData();
},
mounted() {},
methods: {
async _initData() {
this.$vux.loading.show({
text: ''
})
this.loading=true;
await this.$http.get('/quote/list?pageNum=' + this.pageNum).then((response) => {
let result = response.data;
if(result.code === 200) {
this.quoteList.push(...result.data.list);
this.pageNum++
} else if(result.code === 1) {
let vue=this
this.$vux.alert.show({
title: '',
content: result.msg,
onHide() {
vue.$router.push({
name: 'login'
})
}
})
} else {
this.$vux.alert.show({
title: '',
content: result.msg
})
}
}).catch((error) => {
console.log(error)
this.$vux.alert.show({
title: '',
content: ','
})
})
this.loading=false;
this.$vux.loading.hide()
},
toQuotePage(insId) {
this.$router.push({
name: 'quote',
params: {
insId: insId
}
})
},
loadMore() {
this._initData();
}
}
}



